<template>

  <container :title="title">
    <list :editRoute="arctypeRoute" >
      <thead slot="thead">
        <tr>
          <th>{{lang.id}}</th>
          <th>{{lang.typename}}</th>
          <th>{{lang.mid}}</th>
          <th>{{lang.dirs}}</th>
          <th>{{lang.status}}</th>
          <th>{{lang.sorts}}</th>
          <th width="255">{{$t('operation.name')}}</th>
        </tr>
      </thead>
      <tbody slot="tbody">
        <tr v-for="vol in list">
          <td>{{vol.id}}</td>
          <td>{{vol.typename}}</td>
          <td>{{vol.modname}}</td>
          <td>{{vol.dirs}}</td>
          <v-td type="status" :value="vol.status" />
          <td>{{vol.sorts}}</td>
          <v-td type="opt" :value="vol.id"
                :model_id="vol.model_id"
                :jumpArctype="vol"
                archive-route="article_create_edit"
                edit-route="category_create_edit" />
        </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
    import {Container,List, VTd} from "@/components"

    export default {
      components: {VTd, List, Container},
      data(){
        return{
          title:this.$t('arctype.index'),
          lang:this.$t('arctype'),
          arctypeRoute:{
            name:"category_create_edit",
          }
        }
      },
      computed:{
        list(){
          return this.$store.state.table.list
        }
      },
      mounted(){
        const url = '/api/arctype/index'
        this.$store.commit('table/SET_URL', url)
        this.$store.dispatch('table/fetchList')
      }
    }
</script>



